<template>
  <div class="frombox">
    <!-- <h3></h3>
    <hr> -->
    <div class="UserIput">
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="">
          <el-input v-model="formInline.user" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="formInline.region" placeholder="">
            <el-option label="" value="shanghai"></el-option>
            <el-option label="" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="listbar">
      <el-table
      :data="tableData"
      border
      style="width: 100%">
      <el-table-column
        fixed
        prop="date"
        label=""
        align="center"
        width="150">
      </el-table-column>
      <el-table-column
        prop="name"
        label=""
        align="center"
        width="120">
      </el-table-column>
      <el-table-column
        prop="province"
        label=""
        align="center"
        width="120">
      </el-table-column>
      <el-table-column
        prop="city"
        label=""
        align="center"
        width="120">
      </el-table-column>
      <el-table-column
        prop="address"
        align="center"
        label=""
        width="300">
      </el-table-column>
      <el-table-column
        prop="zip"
        label=""
        align="center"
        width="120">
      </el-table-column>
      <el-table-column
        fixed="right"
        label=""
        align="center"
        width="100">
        <template slot-scope="scope">
          <el-button @click="open4" type="text" size="small"></el-button>
          <el-button type="text" size="small" @click="open2"></el-button>
        </template>
      </el-table-column>
    </el-table>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        formInline: {
          user: '',
          region: ''
        },
        tableData: [{
          date: '2016-05-03',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }, {
          date: '2016-05-02',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }, {
          date: '2016-05-04',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }, {
          date: '2016-05-01',
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          zip: 200333
        }]
      }
    },
    name: 'List',
    methods: {
      onSubmit () {
        // console.log('submit!')
      },
      open4 () {
        const h = this.$createElement
        this.$msgbox({
          title: '',
          message: h('p', null, [
            h('span', null, ' '),
            h('i', { style: 'color: teal' }, '')
          ]),
          showCancelButton: true,
          confirmButtonText: '',
          cancelButtonText: '',
          beforeClose: (action, instance, done) => {
            if (action === 'confirm') {
              instance.confirmButtonLoading = true
              // instance.confirmButtonText = '...'
              setTimeout(() => {
                done()
                setTimeout(() => {
                  instance.confirmButtonLoading = false
                })
              })
            } else {
              done()
            }
          }
        })
      },
      open2 () {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '!'
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      }
    }
  }
</script>

<style lang="scss" scode>
  .frombox{
    cursor: pointer;
    box-sizing: border-box;
    width: 100%;
  }
  .UserIput{
    display:flex;
  }
  h3{
    width:100%;
  }
</style>



