<template>
  <div class="page-From">
    <h3></h3>
    <el-form :inline="true" :model="formInline" class="demo-form-inline">
      <el-form-item label="">
        <el-input v-model="formInline.name" placeholder="" @input="onSearch"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formInline.tel" placeholder="" @input="onSearch"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formInline.address" placeholder="" @input="onSearch"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click = "onSearch"></el-button>
      </el-form-item>
      <el-button type="success" @click = "showAddDia" class="btnPush"><i class="el-icon-plus"></i></el-button>
    </el-form>

    <el-table :data="tableData" border stripe style="width: 100%">
      <el-table-column type="index" width="180"></el-table-column>
      <el-table-column prop="date" label="" width="180"></el-table-column>
      <el-table-column prop="name" label="" width="180"></el-table-column>
      <el-table-column prop="tel" label="" width="180"></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
      <el-table-column fixed="right" label="" width="180">
          <template slot-scope="scope">
              <el-button type="text" @click="editUser(scope.row)" size="small"></el-button>
              <el-button type="text" @click="deleConfirm(scope.row)" size="small" class="del-text-btn"></el-button>
          </template>
      </el-table-column>
    </el-table>

    <el-dialog :title = 'editStatus === "add" ? "" : ""' :visible.sync = "editVisible">
      <el-form :model="editorUser" class="editor-form" label-position="top">
        <h4></h4>
        <el-form-item label="">
          <el-input v-model="editorUser.name" size="small" class="el-input"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editorUser.tel" size="small" class="el-input"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editorUser.address" size="small" class="el-input"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dislog-footer">
        <el-button @click="editVisible = false"></el-button>
        <el-button type="primary" @click="confirmEdit"></el-button>
      </div>
    </el-dialog>
   </div>
</template>

<script>
  export default {
    data () {
      return {
        formInline: {
          name: '',
          tel: '',
          address: ''
        },
        editStatus: 'add',
        editVisible: false,
        editorUser: {
          uid: '',
          date: '',
          name: '',
          tel: '',
          address: ''
        },
        initUser: {
          uid: '',
          name: '',
          date: '',
          tel: '',
          address: ''
        },
        tableData: [],
        allTableData: [{
          uid: 1,
          date: '2016-05-03',
          name: '',
          address: ' 1518 ',
          tel: '13566889897'
        }, {
          uid: 2,
          date: '2016-05-03',
          name: '',
          address: ' 1518 ',
          tel: '13866889897'
        }, {
          uid: 3,
          date: '2016-05-03',
          name: '',
          address: ' 1518 ',
          tel: '13966889897'
        }, {
          uid: 4,
          date: '2016-05-03',
          name: '',
          address: ' 1518 ',
          tel: '17166889897'
        }]
      }
    },
    created () {
      this.tableData = this.allTableData
    },
    methods: {
      onSearch () {
        let arr = []
        this.allTableData.forEach((ele) => {
          let s = this.formInline
          let operator = true
          if (s.name) {
            operator = operator && (ele.name.indexOf(s.name) > -1)
          }
          if (s.tel) {
            operator = operator && (ele.tel.indexOf(s.tel) > -1)
          }
          if (s.address) {
            operator = operator && (ele.address.indexOf(s.address) > -1)
          }
          if (operator) {
            arr.push(ele)
          }
        })
        this.tableData = arr
      },
      showAddDia () {
        this.editorUser = Object.assign({}, this.initUser)
        this.editStatus = 'add'
        this.editVisible = true
      },
      deleConfirm (row) {
        console.log(row)
        this.$confirm('' + row.name + ', ?', '', {
          confirmButtonText: '',
          concelButtonText: '',
          type: 'warning'
        }).then(() => {
          let newArr = []
          console.log(this.tableData)
          this.tableData.forEach(temp => {
            if (temp.uid !== row.uid) {
              newArr.push(temp)
            }
          })
          this.tableData = newArr
          this.$message({
            type: 'success',
            message: ''
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },
      editUser (row) {
        this.editorUser = Object.assign({}, row)
        this.editStatus = 'edit'
        this.editVisible = true
      },
      confirmEdit () {
        if (this.editStatus === 'add') {
          let newUser = Object.assign({}, this.editorUser)
          newUser['uid'] = this.allTableData.length + 1
          this.allTableData.push(newUser)
          this.editorUser = Object.assign({}, this.initUser)
          this.$message({
            type: 'success',
            message: ''
          })
        } else {
          this.tableData.forEach((ele, i) => {
            if (ele.uid === this.editorUser.uid) {
            // this.tableData[i] = this.editorUser
            // 
              this.$set(this.tableData, i, this.editorUser)
            }
          })
          this.$message({
            type: 'success',
            message: ''
          })
        }
        this.editVisible = false
      }
    }
  }
</script>

<style lang="scss" scope>
  .page-From{
        box-sizing: border-box;
        width: 100%;
        margin-left: 0;
        text-align: left;
         h3{
            height: 50px;
            line-height: 50px;
            border-bottom: 1px solid #ccc;
        }
        form{
          padding:20px;
        }
        .btnPush{
          float:right;
          margin-right:20px;
        }
        .editor-form .el-input{
          max-width:664px;
        }
        .el-form-item{
          margin-bottom: 0px;
        }
        h4{
          border-bottom:1px solid #ccc;
        }
  }
 
</style>


