<template>
    <div class="account">
        <div class="account-fm">
            <el-form :inline="true" :model="formInlin">
                <el-input placeholder="" v-model="formInlin.name" class="input-with-select" @input="onSearch">
                    <el-select v-model="select" slot="prepend" placeholder="">
                    <el-option label="" value="1"></el-option>
                    <el-option label="" value="2"></el-option>
                    <el-option label="" value="3"></el-option>
                    </el-select>
                    <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
            </el-form>
            <div class="butCreate">
                <el-button type="success" class="create-btn" @click="showAdd"><i class="el-icon-plus"></i></el-button>
                <el-button type="success" class="create-more" @click="editVisibletwo = true"><i class="el-icon-document"></i></el-button>
            </div>   
        </div>
        <el-table class="tableData"
            :data="tableData"
            border
            style="width: 100%">
            <el-table-column
            fixed
            prop="tel"
            label=""
            width="150"
            align="center">
            </el-table-column>
            <el-table-column
            prop="name"
            label=""
            width="150"
            align="center">
            </el-table-column>
            <el-table-column
            prop="province"
            label=""
            width="150"
            align="center">
            </el-table-column>
            <el-table-column
            prop="date"
            label=""
            align="center">
            </el-table-column>
            <el-table-column
            fixed="right"
            label=""
            width="150"
            align="center">
            <template slot-scope="scope">
                <el-button type="text" size="small" class="del-text-btn" disabled></el-button>
                <el-button type="text" size="small" @click="editUser(scope.row)"></el-button>
            </template>
            </el-table-column>
        </el-table>

        <el-dialog :title = 'editorUsers === "add" ? "" : ""' :visible.sync = "editVisible">
            <el-form :model = "editorUser">
                <el-form-item label="">
                    <el-input v-model ="editorUser.name" class="el-input"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model ="editorUser.tel" class="el-input"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model ="editorUser.province" class="el-input"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer">
                <el-button @click="editVisible= false"></el-button>
                <el-button type="primary" @click="confirmEdit"></el-button>
            </div>
        </el-dialog>

        <el-dialog :visible.sync = "editVisibletwo">
         <template>
           <el-form :model = "batchUser" class="batchUser">
             <el-form-item class="batchUsers">
               <el-button type="primary" class="returnBtn"></el-button>
               <h3></h3>
             </el-form-item>     
           <div>
            <div>
              <span class="batchTile pr20p"></span>
              <span></span>
           </div>
           <div class="excal pt40p pb40p">
             <el-button>Excal</el-button>
             <span>Excal</span>
           </div>
           <div class="pasBtn mt20p">
            <span class="batchTile pr20p"></span>
            <span></span>
           </div>
           <div class="inputPas">
             <el-form-item label="">
                <el-input type="text" v-model ="password" placeholder="6-20"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input type="text" v-model ="newpassword" placeholder="6-20"></el-input>
              </el-form-item>
            </div>
           </div>           
           <el-button type="success"></el-button>
          </el-form>
         </template>
        </el-dialog>
    </div>
    
</template>

<script>
  export default {
    data () {
      return {
        formInlin: {
          name: '',
          tel: '',
          date: '',
          province: ''
        },
        editorUsers: 'add',
        editVisible: false,
        editVisibletwo: false,
        editorUser: {
          name: '',
          tel: '',
          date: '',
          province: ''
        },
        initUser: {
          uid: '',
          name: '',
          tel: '',
          data: '',
          province: ''
        },
        tableData: [],
        allTableData: [{
          tel: '13568759898',
          date: '2016-05-03',
          name: '',
          province: ''
        }, {
          tel: '13568759898',
          date: '2016-05-02',
          name: '',
          province: ''
        }, {
          tel: '13668759898',
          date: '2016-05-04',
          name: '',
          province: ''
        }, {
          tel: '13868759898',
          date: '2016-05-01',
          name: '',
          province: ''
        }]
      }
    },
    created () {
      this.tableData = this.allTableData
    },
    methods: {
      onSearch () {
        let arr = []
        this.allTableData.forEach((ele) => {
          console.log('allTableData')
          let s = this.formInlin
          let operator = true
          if (s.name) {
            operator = operator && (ele.name.indexOf(s.name) > -1)
          }
          if (operator) {
            arr.push(ele)
          }
        })
        this.tableData = arr
      },
      showAdd () {
        this.editorUser = Object.assign({}, this.initUser)
        this.editStatus = 'add'
        this.editVisible = true
      },
      editUser (row) {
        this.editorUser = Object.assign({}, row)
        this.editStatus = 'edit'
        this.editVisible = true
      },
      confirmEdit () {
        if (this.editStatus === 'add') {
          let newUser = Object.assign({}, this.editorUser)
          newUser['uid'] = this.allTableData.length + 1
          this.allTableData.push(newUser)
          this.editorUser = Object.assign({}, this.initUser)
          this.$message({
            type: 'success',
            message: ''
          })
        } else {
          this.tableData.forEach((ele, i) => {
            if (ele.uid === this.editorUser.uid) {
              this.$set(this.tableData, i, this.editorUser)
            }
          })
          this.$message({
            type: 'success',
            message: ''
          })
        }
        this.editVisible = false
      },
      batch () {
        this.editVisibletwo = true
      }
    }
  }
</script>

<style lang="scss" scope>
.account-fm {
    width: 100%;
    display: flex;
    justify-content:space-between;
}
.tableData{
    margin-top:20px;
}

.el-form-item__content {
  display:flex;
    h3{
    margin: 0 auto;
  }
}
.batchTile{
  font-weight:bold;
  color:blue;
}
.excal{
    display: flex;
    justify-content: space-evenly;
}
.pasBtn,.inputPas{
  display:flex;
}

.inputPas{
    flex-direction: column;
    width: 50%;
}

</style>


