<template>
 <div>
     <el-form :inline = "true" :model="InputUser" ref="form" class="InputUser">
         <el-form-item label="">
             <el-input v-model="InputUser.name" placeholder="" @input="onSeach"></el-input>
         </el-form-item>
          <el-form-item label="">
             <el-input v-model="InputUser.tel" placeholder="" @input="onSeach"></el-input>
         </el-form-item>
          <el-form-item label="">
             <el-input v-model="InputUser.date" placeholder="" @input="onSeach"></el-input>
         </el-form-item>
          <el-form-item label="">
             <el-input v-model="InputUser.address" placeholder="" @input="onSeach"></el-input>
         </el-form-item>
          <el-form-item label="">
             <el-input v-model="InputUser.province" placeholder="" @input="onSeach"></el-input>
         </el-form-item>
         <el-form-item>
             <el-button @click="onSeach"></el-button>
         </el-form-item>
     </el-form>
    <el-table
    :data="tableData"
    border
    style="width: 100%">
    <el-table-column
      fixed
      prop="date"
      label=""
      align="center"
      width="150">
    </el-table-column>
    <el-table-column
      prop="name"
      label=""
      align="center"
      width="150">
    </el-table-column>
    <el-table-column
      prop="province"
      label=""
      align="center"
      width="150">
    </el-table-column>
    <el-table-column
      prop="tel"
      label=""
      align="center"
      width="120">
    </el-table-column>
    <el-table-column
      prop="address"
      align="center"
      label="">
    </el-table-column>
    <el-table-column
      fixed="right"
      label=""
      align="center"
      width="100">
      <template slot-scope="scope">
        <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
        <el-button type="text" size="small"></el-button>
      </template>
    </el-table-column>
  </el-table>
 </div>
</template>

<script>
export default {
  data () {
    return {
      InputUser: {
        name: '',
        tel: '',
        date: '',
        address: '',
        province: ''
      },
      tableData: [],
      alltableData: [{
        date: '2014-05-03',
        name: '',
        province: '',
        tel: '13934323434',
        address: ' 1518 ',
        zip: 200333
      }, {
        date: '2015-05-02',
        name: '',
        province: '',
        tel: '13534323434',
        address: ' 1518 ',
        zip: 200333
      }, {
        date: '2016-05-04',
        name: '',
        province: '',
        tel: '13834323434',
        address: ' 1518 ',
        zip: 200333
      }, {
        date: '2016-05-01',
        name: '',
        province: '',
        tel: '13834323434',
        address: ' 1518 ',
        zip: 200333
      }]
    }
  },
  created () {
    this.tableData = this.alltableData
  },
  methods: {
    handleClick (row) {
    },
    onSeach () {
      let arr = []
      this.alltableData.forEach((ele) => {
        let s = this.InputUser
        let operator = 'true'
        if (s.name) {
          operator = operator && (ele.name.indexOf(s.name) > -1)
        }
        if (s.tel) {
          operator = operator && (ele.tel.indexOf(s.tel) > -1)
        }
        if (s.date) {
          operator = operator && (ele.date.indexOf(s.date) > -1)
        }
        if (s.address) {
          operator = operator && (ele.address.indexOf(s.address) > -1)
        }
        if (s.province) {
          operator = operator && (ele.address.indexOf(s.province) > -1)
        }
        if (operator) {
          arr.push(ele)
        }
      })
      this.tableData = arr
    }
  }
}
</script>

<style lang="scss" scope>
.InputUser{
    display:flex;
}
</style>


