<template>
  <div>
      <el-form :inline ="true" :model="UserData" ref="form" class="UserData">
          <el-form-item label="">
              <el-input placeholder="" v-model="UserData.name" @input="onSeach"></el-input>
          </el-form-item>
          <el-form-item label="">
              <el-input placeholder="" v-model="UserData.address" @input="onSeach"></el-input>
          </el-form-item>
          <el-form-item>
              <el-button @click="onSeach"></el-button>
          </el-form-item>
      </el-form>
      <el-table
    :data="tableData"
    border
    style="width: 100%">
    <el-table-column
      prop="date"
      label=""
      width="180"
      align="center">
    </el-table-column>
    <el-table-column
      prop="name"
      label=""
      width="180"
      align="center">
    </el-table-column>
    <el-table-column
      prop="address"
      label=""
      align="center">
    </el-table-column>
  </el-table>
  </div>
</template>

<script>
export default {
  data () {
    return {
      UserData: {
        name: '',
        address: ''
      },
      tableData: [],
      alltableData: [{
        date: '2016-05-03',
        name: '',
        province: '',
        city: '',
        address: ' 1518 ',
        zip: 200333,
        tag: ''
      }, {
        date: '2016-05-02',
        name: '',
        province: '',
        city: '',
        address: ' 1518 ',
        zip: 200333,
        tag: ''
      }, {
        date: '2016-05-04',
        name: '',
        province: '',
        city: '',
        address: ' 1518 ',
        zip: 200333,
        tag: ''
      }, {
        date: '2016-05-01',
        name: '',
        province: '',
        city: '',
        address: ' 1518 ',
        zip: 200333,
        tag: ''
      }]
    }
  },
  created () {
    this.tableData = this.alltableData
  },
  methods: {
    onSeach () {
      let arr = []
      this.alltableData.forEach((ele) => {
        let s = this.UserData
        if ((ele.name.indexOf(s.name) > -1) && (ele.address.indexOf(s.address) > -1)) {
          arr.push(ele)
        }
      })
      this.tableData = arr
    }
  }
}
</script>

<style lang="scss" scope>
.UserData{
    display:flex;
}
</style>


